.app-background {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  text-align: center;
  background-color: #090221;
  color: white;
  justify-content: flex-start;
  align-items: center;
}

.task-container {
  display: flex;
  max-width: 70vh;
  height: 100%;
  flex-direction: column;
  text-align: center;
  background-color: white;
  color: #090221;
  border-radius: 8px;
  justify-content: flex-start;
  align-items: center;
  padding: 30px 20px;
  margin: 0 auto 20px;
  min-height: 205px;
}

.heading-text {
  color: #090221;
  font-size: 30px;
  font-weight: bolder;
  text-align: center;
  margin: 50px;
  background-color:white;
  padding: 15px 25px;
  border-radius: 8px;
}

.text-input {
  border: #090221 1px solid;
  outline: none;
  border-radius: 8px;
  width: 310px;
  font-size: 18px;
  padding: 10px 15px;
  margin: 0 15px 15px 15px;
  font-weight: bold;
}

.btn {
  background-color: #090221;
  outline: none;
  border: none;
  color: white;
  font-size: 17px;
  font-weight: bolder;
  padding: 10px 15px;
  border-radius: 8px;
  text-align: center;
  margin: 0 15px 15px 15px;
  cursor: pointer;
}

.no-item-text {
  color: #090221;
  font-size: 17px;
  font-weight: bolder;
  text-align: center;
  margin: 50px;
}

.delete-icon {
  height: 26px;
  cursor: pointer;
  color: white;
}

.list-item {
  background-color: #ABB2EC;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: none;
  border-radius: 8px;
  width: 450px;
  font-size: 18px;
  padding: 10px 15px;
  margin: 15px 0;
  color: #131A28;
  font-weight: bold;
  text-align: start;
  align-items: center;
  cursor: pointer;
}

.footer {
  color: white;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  margin: 10px;
  padding: 15px 25px;
}
.task-complete{
  text-decoration: line-through;
  color: white;
}
@media only screen and (max-width: 600px) {
  .heading-text {
    color: #090221;
      font-size: 25px;
      font-weight: bolder;
      text-align: center;
      margin: 50px;
      background-color: white;
      padding: 15px 25px;
      border-radius: 8px;
  }

  .text-input {
       border: #090221 1px solid;
      outline: none;
      border-radius: 8px;
      width: 200px;
      font-size: 16px;
      padding: 8px 12px;
      margin: auto 8px;
      font-weight: bold;
  }

  .list-item {
    background-color: #ABB2EC;
      border: none;
      border-radius: 8px;
      width: 290px;
      font-size: 18px;
      padding: 10px 15px;
      margin: 15px 0;
      color: #131A28;
      font-weight: bold;
      text-align: start;
      align-items: center;
  }

  .delete-icon {
      height: 18px;
      cursor: pointer;
  }
}

